<template>
  <el-menu router :default-active="'/'" class="el-menu-demo" mode="horizontal" @select="handleSelect" v-if="user">
    <el-menu-item index="/"></el-menu-item>
    <el-submenu index="2">
      <template slot="title"></template>
      <el-menu-item index="/assets/add"></el-menu-item>
      <el-menu-item index="/assets/list"></el-menu-item>
    </el-submenu>
    <el-menu-item index="/task/list"></el-menu-item>
    <el-menu-item index="/dict/list"></el-menu-item>
    <el-submenu index="5">
      <template slot="title"></template>
      <el-menu-item index="/plugins/add"></el-menu-item>
      <el-menu-item index="/plugins/list"></el-menu-item>
    </el-submenu>
    <el-submenu v-if="user.type === 'ROOT'" index="6">
      <template slot="title"></template>
      <el-menu-item index="/user/add"></el-menu-item>
      <el-menu-item index="/user/list"></el-menu-item>
    </el-submenu>
    <el-menu-item index="/user/pwd"></el-menu-item>
    <el-menu-item><el-tag effect="plain" type="info">{{ user.user_name }}</el-tag></el-menu-item>
    <el-menu-item><el-link @click="logout" type="danger"></el-link></el-menu-item>
  </el-menu>
  <el-menu router v-else class="el-menu-demo" mode="horizontal">
    <el-menu-item></el-menu-item>
    <el-menu-item disabled></el-menu-item>
  </el-menu>
</template>

<script>
export default {
  name: 'Header',
  created () {
    this.getUser()
  },
  data () {
    return {
      user: this.$store.state.user
    }
  },
  watch: {
    '$store.state.user': function () {
      this.user = this.$store.state.user
    }
  },
  methods: {
    handleSelect () {},
    getUser () {
      let that = this
      this.$http.get('/get/user').then(res => {
        that.$store.commit('put_user', res.data.data)
      })
    },
    logout () {
      let that = this
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        that.$http.post('/logout').then(res => {
          that.$store.commit('put_user', false)
          that.$router.push('/login')
        })
      }).catch(() => {})
    }
  }
}
</script>

<style scoped>

</style>



