<template>
  <div>
    <el-form :inline="true" :model="form" @keyup.enter.native="loadData(1)" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="form.user_name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.state" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="NORMAL"></el-option>
          <el-option label="" value="BANED"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.type" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="NORMAL"></el-option>
          <el-option label="" value="ROOT"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="loadData(1)"></el-button>
      </el-form-item>
    </el-form>
    <el-table
      ref="filterTable"
      @selection-change="handleSelectionChange"
      :data="tableData.items">
      <el-table-column
        prop="user_name"
        label="">
      </el-table-column>
      <el-table-column
        prop="type"
        label=""
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag effect="plain" type="warning" v-if="scope.row.type === 'ROOT'"></el-tag>
          <el-tag effect="plain" type="info" v-if="scope.row.type === 'NORMAL'"></el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="state"
        label=""
        filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag effect="plain" type="danger" v-if="scope.row.state === 'BANED'"></el-tag>
          <el-tag effect="plain" type="success" v-if="scope.row.state === 'NORMAL'"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
          <el-button
            size="mini"
            type="info"
            v-if="scope.row.state === 'NORMAL'"
            @click="handleState(scope.$index, scope.row, 'BANED')"></el-button>
          <el-button
            size="mini"
            type="warning"
            v-if="scope.row.state === 'BANED'"
            @click="handleState(scope.$index, scope.row, 'NORMAL')"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="padding-top: 10px"
      background
      layout="prev, pager, next"
      @current-change="loadData"
      :current-page.sync="tableData.page"
      :page-size="tableData.page_size"
      :total="tableData.total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'Users',
  data () {
    return {
      tableData: [],
      form: {
        user_name: '',
        state: '',
        type: ''
      }
    }
  },
  created () {
    this.loadData(1)
  },
  methods: {
    loadData (next) {
      let that = this
      this.$http.get('/query/user', { params: { next: next, user_name: this.form.user_name, state: this.form.state, type: this.form.type } }).then(res => {
        that.tableData = res.data.data
      })
    },
    handleSelectionChange (val) {
      this.selected = val
    },
    handleEdit () {},
    handleDelete (index, row) {
      let that = this
      this.$confirm(': ' + row.user_name, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        that.$http.post('/del/user', { 'user_name': row.user_name }).then(res => {
          that.$message.success('')
          that.tableData.items.splice(index, 1)
        })
      }).catch(() => {})
    },
    handleState (index, row, state) {
      let that = this
      this.$confirm('' + (state === 'BANED' ? ' "" ' : ' "" ') + ': ' + row.user_name, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        that.$http.post('/modify/state', { 'user_name': row.user_name, 'state': state }).then(res => {
          that.$message.success('')
          that.tableData.items[index].state = state
        })
      }).catch(() => {})
    }
  }
}
</script>

<style scoped>
  .item {
    margin-top: 10px;
    margin-right: 40px;
  }
</style>



