<template>
  <div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="" prop="title">
        <el-input v-model="form.title" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="remark">
        <el-input type="textarea" v-model="form.remark"></el-input>
      </el-form-item>
      <el-form-item label="" prop="script">
        <el-input type="textarea" :autosize="{ minRows: 20, maxRows: 140}" v-model="form.script"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.label" multiple placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">{{ id !== undefined ? '' : '' }}</el-button>
        <el-button @click="id = undefined"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'PluginCreate',
  data () {
    return {
      options: [
        { value: '', name: 'emergency' },
        { value: '', name: 'danger' },
        { value: '', name: 'warning' },
        { value: '', name: 'info' }
      ],
      form: {
        title: '',
        remark: '',
        script: '',
        label: []
      },
      id: this.$route.params.id
    }
  },
  created () {
    this.viewPlugin(this.id)
  },
  methods: {
    viewPlugin (id) {
      if (id !== null && id !== undefined) {
        let that = this
        this.$http.post('/view/plugin', { 'id': id }).then(res => {
          that.form = res.data.data
        })
      }
    },
    onSubmit () {
      if (this.id !== null && this.id !== undefined) {
        this.form.id = this.id
      } else {
        this.form.id = undefined
      }
      let that = this
      this.$http.post('/add/plugin', this.form).then(res => {
        that.$message.success('')
        that.$router.push('/plugins/list')
      })
    }
  }
}
</script>

<style scoped>

</style>



