<template>
  <div>
    <el-form v-loading="loading" ref="form" :model="form" label-width="80px">
      <el-form-item label="IP">
        <el-input v-model="form.ip" placeholder="/IP eg: 10.12.13.24 IP eg: 10.12.13.24,10.12.13.25 IP eg: 10.12.13.0/24 IP eg: 10.12.13.1-10.12.13.20"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.region" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="cn"></el-option>
          <el-option label="" value="hk"></el-option>
          <el-option label="" value="us"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.tags" filterable allow-create multiple placeholder="/">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'AssetsCreate',
  data () {
    return {
      options: [],
      loading: false,
      form: {
        ip: '',
        region: '',
        tags: []
      }
    }
  },
  created () {

  },
  methods: {
    onSubmit () {
      let that = this
      this.loading = true
      this.$http.post('/add/ip', this.form).then(() => {
        that.loading = false
        that.$message.success('')
      })
    }
  }
}
</script>

<style scoped>

</style>



